
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 1;
}
#app.spinning {
  opacity: 0.6;
}
#spinner {
  position: fixed;
  z-index: 1000;
  top: 15px;
  right: 16px;
}
#themer {
  position: fixed;
  top: 8px;
  right: 46px;
  z-index: 2000;
}
@media only screen
and (min-width : 1024px) {
#themer {
}
#spinner {
}
}

.capitalize {
  text-transform: capitalize;
}
.navbar {
  font-size: 16px;
}
.navbar p {
  margin: 1em 0;
}












































#write {
  max-width: 40em;
  margin: 0 auto;
  padding-top: 2em 60px;
  min-height: calc(100vh - 6em);
}
.sidebar {
  display: none;
  position: fixed;
  right: 0;
  top: 100px;
  bottom: 5px;
  width: 240px;
  margin: 0;
  font-size: 12px;
  border-left: 1px solid #eee;
  overflow-y: auto;
}
@media only screen
and (min-width : 1200px) {
.main.with-sidebar {
    padding-right: 60px;
}
.sidebar, #toc-button {
    display: block;
}
}
#back-top {
  margin: 10px 18px;
}
#toc > ul {
  padding: 0 8px;
  margin: 0;
}
#toc li {
  list-style: none;
  text-align: initial;
  margin-top: 5px;
  margin-bottom: 5px;
  line-height: 18px;
}
#toc a {
  color: inherit;
  text-align: left;
  text-decoration: none;
  font-size: 12px;
}
#toc li.toc-visible {
  font-weight: bold;
}
#toc li.toc-h1 {
  opacity: .6;
  margin: 10px;
}
#toc li.toc-h1.toc-visible {
  font-weight: normal;
  opacity: 1;
}
#toc li.toc-h1 a {
  font-size: 16px;
}
#toc li.toc-h2 {
  padding-left: 10px;
}
#toc li.toc-h2 a {
  font-size: 14px;
}
#toc li.toc-h3 {
  padding-left: 25px;
}

.footer[data-v-52dc11de] {
  position: relative;
  font-size: 90%;
  font-style: italic;
  z-index: 1000;
}
.footer p[data-v-52dc11de] {
  text-align: center;
}
